* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  background: radial-gradient(circle, rgba(18, 137, 189, .63) 0%, rgba(3, 216, 244, .84) 35%, #92e8d9 100%);
  font-family: Noto Sans, sans-serif;
}

.wrapper {
  justify-content: space-evenly;
  align-items: center;
  gap: 30px;
  margin-top: 100px;
  display: flex;
}

.location {
  justify-content: center;
  gap: 50px;
  display: flex;
}

.location p {
  color: rgba(0, 0, 0, .89);
  font-size: 3rem;
  font-weight: 800;
}

.search-form {
  justify-content: center;
  gap: 50px;
  padding-top: 30px;
  padding-bottom: 30px;
  display: flex;
}

.search-form input[type="text"] {
  border: 2px solid #ccc;
  border-radius: 5px;
  outline: none;
  padding: 10px;
  font-size: 16px;
  transition: border-color .3s;
}

.search-form input[type="text"]:focus {
  border-color: #00bcd4;
}

.search-form button[type="submit"] {
  color: #fff;
  cursor: pointer;
  background-color: #00bcd4;
  border: none;
  border-radius: 5px;
  padding: 10px 20px;
  font-size: 16px;
  transition: background-color .3s;
}

.search-form button[type="submit"]:hover {
  background-color: #0097a7;
}

